export const InputField = () => {
  const [value, setValue] = React.useState(30);
  const handleChange = (event, newValue) => {
    setValue(newValue);
    action("onChangeInput")(newValue);
  };
  const handleInputChange = event => {
    const v = event.target.value;
    setValue(v === "" ? "" : Number(v));
    action("onChangeInput")(v);
  };
  const handleBlur = () => {
    if (value < 0) {
      setValue(0);
      action("onChangeInput")(0);
    } else if (value > 100) {
      setValue(100);
      action("onChangeInput")(100);
    }
  };
  return <>
      <Slider defaultValue={40} startIcon={<VolumeUpIcon />} endIcon={<VolumeDownIcon />} />
      <Slider defaultValue={40} startIcon={<VolumeUpIcon />} value={value} onChange={handleChange} inputCmp={<Input value={value} size="small" onChange={handleInputChange} onBlur={handleBlur} inputProps={{
      step: 10,
      min: 0,
      max: 100,
      type: "number"
    }} />} />
      <Slider defaultValue={40} startIcon={<VolumeUpIcon />} endIcon={<VolumeDownIcon />} value={value} onChange={handleChange} inputCmp={<Input value={value} size="small" onChange={handleInputChange} onBlur={handleBlur} inputProps={{
      step: 10,
      min: 0,
      max: 100,
      type: "number"
    }} />} />
    </>;
};
